.App {
    text-align: center;
}

.app-title {
    font-size: 30px;
    font-weight: bolder;
    border: 1px solid black;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    word-spacing: 3.5px;
}

.section {
    height: auto;
    width: 100%;
    padding: 2rem;
}

.section-heading {
    font-size: 20px;
    font-weight: bolder;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    word-spacing: 2.5px;
}

.section-subheading {
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 1px;
    word-spacing: 2px;
}


